<template>
  <div id="app">
    <div v-show="sum_ave_len>100">
      图例如下(图片与下方示例无关)：
      <br>
      <el-image src="https://s2.loli.net/2022/04/25/jN6efZX2OQEcJyR.png"></el-image>
    </div>
    <el-button @click="begin">{{text}}</el-button>
    <br>
    平均距离：{{sum_ave_len}}
    <br>
    <canvas id="canvas" :width="window_width" :height="window_width" style="border:1px solid #000000;"></canvas>
  </div>
</template>

<script>


export default {
  name: 'App',
  components: {
  },
  created() {
    this.c_points = new Array(this.c_num)
    for(let i=0;i<this.c_num;i++){
      this.c_points[i]=new Array(2)
    }
    this.s_points = new Array(this.s_num)
    for(let i=0;i<this.s_num;i++){
      this.s_points[i]=new Array(2)
    }
    for(let i=0;i<this.c_num;i++){
      let key = true;
      while (key) {
        key = false
        var a = [Math.floor((Math.random() * this.window_width) + 1), Math.floor((Math.random() * this.window_width) + 1)];
        for (let j = 0; j < this.c_num; j++) {
          if (this.lens(a, this.c_points[j]) < 10) {
            key = true
          }
        }
        if (key === false) {
          for (let j = 0; j < this.s_num; j++) {
            if (this.lens(a, this.s_points[j]) < 10) {
              key = true
            }
          }
        }
      }
      this.c_points[i]=a
    }
    for(let i=0;i<this.s_num;i++){
      let key = true;
      while (key) {
        key = false
        var b = [Math.floor((Math.random() * this.window_width) + 1), Math.floor((Math.random() * this.window_width) + 1)];
        for (let j = 0; j < this.c_num; j++) {
          if (this.lens(b, this.c_points[j]) < 5) {
            key = true
          }
        }
        if (key === false) {
          for (let j = 0; j < this.s_num; j++) {
            if (this.lens(b, this.s_points[j]) < 5) {
              key = true
            }
          }
        }
      }
      this.s_points[i]=b
    }
  },
  data(){
    return{
      text:"开始",
      window_width:800,
      c_num:150,
      s_num:30,
      c_points:[],
      s_points:[],
      c_points_belong:[],
      s_points_ave_len:[],
      sum_ave_len:0,
    }
  },
  methods:{
    // 开始
    begin(){
      if(this.sum_ave_len<=100){
        if(this.text==="开始"){
          this.start_open()
        }else {
          this.next_open()
        }
        this.text="继续"
        this.c_points_belong = this.kmeans(this.c_points,this.s_points)
        this.kmeans_draw()
        this.kmeans_ave_len()
        this.cut_s_points()
        if(this.sum_ave_len>100){
          this.end_open()
        }
      }

    },
    // 计算两点距离
    lens(a,b){
      return  Math.sqrt(Math.pow(a[0]-b[0],2)+Math.pow(a[1]-b[1],2))
    },
    // kmeans主程序
    kmeans(a,b){
      const res = new Array(a.length);
      for(let i =0;i<a.length;i++){
        let temp=[]
        for(let j=0;j<b.length;j++){
          temp.push(this.lens(a[i],b[j]))
        }
        res[i]=temp.indexOf(Math.min.apply(null,temp))
      }
      return res
    },
    // 画图
    kmeans_draw(){
      const c = document.getElementById("canvas");
      const cxt = c.getContext("2d");
      cxt.fillStyle="#ffffff";
      cxt.fillRect(0,0,this.window_width,this.window_width);
      cxt.fillStyle="#ffca00";
      for(let i =0;i<this.c_num;i++){
        cxt.beginPath();
        cxt.arc(this.c_points[i][0],this.c_points[i][1],3,0,Math.PI*2,true);
        cxt.closePath();
        cxt.fill();
      }
      cxt.fillStyle="#ff0000";
      for(let i =0;i<this.s_num;i++){
        cxt.beginPath();
        cxt.arc(this.s_points[i][0],this.s_points[i][1],5,0,Math.PI*2,true);
        cxt.closePath();
        cxt.fill();
      }
      for(let i=0;i<this.c_num;i++){
        cxt.strokeStyle="#02fa79";
        cxt.moveTo(this.c_points[i][0],this.c_points[i][1])
        cxt.lineTo(this.s_points[this.c_points_belong[i]][0],this.s_points[this.c_points_belong[i]][1])
        cxt.stroke()
      }
    },
    // 平均距离
    kmeans_ave_len(){
      const sum = new Array(this.s_num);
      sum.fill(0)
      const sum_num = new Array(this.s_num);
      sum_num.fill(0)
      const ave = new Array(this.s_num);
      ave.fill(0)
      for(let i=0;i<this.c_num;i++){
        sum[this.c_points_belong[i]]=sum[this.c_points_belong[i]]+this.lens(this.c_points[i],this.s_points[this.c_points_belong[i]])
        sum_num[this.c_points_belong[i]]=sum_num[this.c_points_belong[i]]+1
      }
      for(let i=0;i<this.s_num;i++){
        if(sum_num[i]>0){
          ave[i]=sum[i]/sum_num[i]
        }
      }
      this.s_points_ave_len = ave
      for(let i=0;i<this.s_points_ave_len.length;i++){
        this.sum_ave_len=this.sum_ave_len+this.s_points_ave_len[i]
      }
      this.sum_ave_len=this.sum_ave_len/this.s_num
    },
    // 去除平均距离最近的提货点
    cut_s_points(){
      this.s_points.splice(this.s_points_ave_len.indexOf(Math.min.apply(null,this.s_points_ave_len)),1)
      this.s_num=this.s_num-1
    },
    // 模拟退火主程序
    // sa(){
    //   const T0 = 3000;
    //   // const r = 0.995
    //   // const Ts = 0.01
    //   // const kl = 500
    //   // const T = T0
    //   // while(T>Ts){
    //   //
    //   // }
    // },
    sum_dis(line){
      var sum=0
      for (let i=0;i<line.length-1;i++){
        sum = sum+this.lens(line[i],line[i+1])
      }
      sum = sum+this.lens(line[0],line[line.length-1])
      return sum
    },
    start_open() {
      this.$alert('随机选取若干红色配送点与橙色客户点，将客户点以配送点为中心聚类，并计算所有客户点到配送点的平均距离。',  {
        confirmButtonText: '确定',
      });
    },
    next_open(){
      this.$notify({
        title: '提示',
        message: '依次去除平均配送距离最少的红色配送点，并重新聚类。'
      });
    },
    end_open(){
      this.$alert('<strong>当平均配送距离达到某个值后，停止去除配送点，将剩下的配送点通过模拟退火算法求出配送路径。</strong>',  {
        dangerouslyUseHTMLString: true
      });
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
